import { useEffect, useState } from 'react';
import logo from '../images/logo.svg';
import Navbar from './Navbar';

const Header = (props) => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const toggler = () => setIsMenuOpen((state) => !state);
  useEffect(() => {
    if (!props.loggedIn) setIsMenuOpen(false);
  }, [props.loggedIn]);

  return (
    <>
      {isMenuOpen && props.loggedIn && <Navbar {...props} isOpen={isMenuOpen} />}
      <header className="header">
        <img className="header__logo" src={logo} alt="" />
        <Navbar {...props} />
        {props.loggedIn && (
          <button className={`header__menu ${isMenuOpen && 'header__menu_active'}`} onClick={toggler}>
            <span></span>
            <span></span>
            <span></span>
          </button>
        )}
      </header>
    </>
  );
};

export default Header;
